@font-face{
  src: url(fonts/bauhaus-93-regular.ttf);
  font-family: bauhaus;
}
nav{
  width: 100%;
  background-color: var(--secondary-color);
  font-size: 1.6em;
  font-family: bauhaus;
  text-align: center;
}
nav ul{
  
  display: flex;
  flex-direction: row;
  height: 45px;
  background-color: var(--secondary-color);
}

nav li{
  margin: auto;
  width: 15%;
  border-radius: 10px;
  list-style: none;
  color:var(--text-color);
  cursor: pointer;
  
}

nav li:hover:nth-child(1) #brand-menu{
  display: flex;
  
}

#brand-menu{
  display: none;
  flex-direction: column;
  width: 200px;
  border-left: 1px solid (--text-color);
  font-size: 1.2em;
}

#brand-menu button {
  display: flex;
  border-bottom: 2px solid (--text-color);
}


#brand-menu button:hover{
  border-right: 2px solid white;
}

#classic-brand-btn{
 background-color: #00003A
}

#bee-brand-btn{
  color: black;
  background-color: #FFF100;
}

#holiday-brand-btn{
  color: #EA4335;
  background-color: #146B3A;
}

#burger{ 
  display: none
}

@media screen and (max-width: 1024px){

  #burger{ 
    display: block;
    width: 50px;
    height: 50px;
  }

  #burger img{
    width: 100%;
    margin: auto;
  }

  nav ul{
    display: none;
    height: auto;
  }

  #brand-menu{
    margin: auto;
    border: none;
    padding: 5px;
  }

  #brand-menu button{
    padding: 7px;
  }

  nav li{
    padding-top: 7px;
    padding-bottom: 7px;
    width: 100%;
    margin: auto;
  }

  .down{
    display: flex;
    flex-direction: column;
  }
}