*{
  margin: 0;
  padding: 0;
}

html {
  scroll-behavior: smooth;
}



:root{
  --main-color: #03004F;
  --secondary-color: #00003A;
  --text-color: white;
}

main{
  display: flex;
  flex-direction: column;
}

button{
  background: none;
  border: none;
  outline: none;
  color: white;
  font-family: bauhaus;
  font-size: .70em;
  cursor: pointer;
}

ul{
  list-style: none;
}

a{
  text-decoration: none;
  color: white
}

a:visited {
  color: white
}