#shop-wrapper{
  display: none;
  flex-direction: row;
  height: 100vh;
}

#gallery-container{
  display: grid;
  grid-template-columns: auto auto auto;
  grid-column-gap: 25px;
  grid-row-gap: 15px;
  border-right: 3px solid black;
  width: 50%;
}

#gallery-container li{
  margin: auto;
  text-align: center;
}

#gallery-container img{
  width: 90%;
  margin: auto;
  cursor: pointer;
}

#gallery-container span{
  display: flex;
  flex-direction: column;
  
}

#gallery-container p{
  display: none;
}

#purchase-container{
  display: none;
  width: 50%;
}

#highlight-container{
  display: flex;
  flex-direction: row;
  margin: auto;
  padding: 35px;
}

#highlight-image-container{
  width: 50%;
  margin: auto;
  padding-right: 5px;
}

#highlight-image-container img{
  width: 100%;
  
}

#highlight-info-container{
  display: flex;
  flex-direction: column;
  width: 50%;
  height: 50vh;
  justify-content: space-around;
  text-align: center;
  border: 3px solid black;
}


#quantity-btns-container{
  width: 75%;
  margin: auto;
  display: flex;
  flex-direction: row;
  justify-content: space-around;
  text-align: center;
}

#quantity-btns-container button{
  width: 75px;
  border-bottom: 1px solid black;
  color: black;
  font-size: 2em;
}

#quantity-btns-container button:hover{
  outline: 1px solid black;
}

#quantity-btns-container button:active{
  background-color: #146B3A;
  transition: .3s ease;
}

#quantity{
  margin: auto;
  font-size: 2em;
}

#cart-btn-container{
  width: 75%;
  margin: 15px auto auto auto;
}

#cart-btn-container button{
  width: 100%;
  color: white;
  font-size: 1.75em;
  background: #146B3A;
}

#checkout-container{
  display: flex;
  flex-direction: row;
  border: 2px solid black;
}

#cart{
  width: 50%;
}

#cart li{
  display: none;
  width: 100%;
  border-right: 2px solid black;
  border-bottom: 2px solid black;
}

#cart img{
  width: 20%;
}

#cart-item-info, #cart-item-btns {
  display: flex;
  flex-direction: column;
  width: 50%;
  justify-content: space-around;
  text-align: center;
}

#cart button{
  color: black;
}


@media screen and (max-width: 1024px){
  #shop-wrapper{
    flex-direction: column;
  }

  #purchase-container{
    width: 100%;
  }
  #gallery-container{
    grid-template-columns: auto auto;
    width: 100%;
  }

  #highlight-container{
    width: 50%;
    height: auto;
    flex-direction: column;
    margin: auto;
  }

  #highlight-info-container{
    width: 100%;
    
  }

  #highlight-image-container{
    width: 100%;
    margin: auto;
    padding-right: 5px;
  }
}

