#section-wrapper{
  display: flex;
  flex-direction: row;
  font-family: Verdana, Geneva, Tahoma, sans-serif;
}

.left-section{
  display: flex;
  flex-direction: column;
  width: 30%;
  height: 100vh;
  background: var(--main-color); 
  background-size: cover;
  z-index: 99;
}

.logo{
  width: 90%;
  margin: auto;
}

.right-section{
 width: 60%;
 margin: auto;
}

#classic-container{
  display: flex;
  flex-direction: row;
  width: 100%;
}

#bee-container{
  display: none;
  flex-direction: row;
  width: 100%;
}

#holiday-container{
  display: none;
  flex-direction: row;
  width: 100%;
}

.open{
  display: flex;
}

header{
  width: 100%;
}

header img{
  margin: auto auto 50px auto;
  display: block;
  border-top: 10px solid var(--main-color); 
  border-bottom: 10px solid var(--main-color); 
  border-radius: 5px;
  width: 100%;
}

header h1{
  text-align: center;
  color: var(--secondary-color); 
  border-bottom: 3px solid var(--main-color);
}

header p{
  font-size: 20px;
  margin: 15px;
}

#featured-products{
  display: flex;
  flex-direction: row;
  margin: auto;
}

#featured-products img{
  margin: 75px auto auto auto;
  width: 20%;
}

@media screen and (max-width: 1024px){
  #section-wrapper{
    flex-direction: column;
    height: auto;
  }

  .left-section{
    width: 100%;
    height: auto;
    background-size: cover;
  }

  .logo{
    width: 45%;
    margin: auto;
  }

  .right-section{
    width: 90%;
  }

  header img{
    margin-top: 15px;
  }

  header{
  font-size: 12px;  
  }

  #classic-container{
    display: flex;
    flex-direction: column;
  }
  
  #bee-container{
    display: none;
    flex-direction: column;
  }
  
  #holiday-container{
    display: none;
    flex-direction: column;
  }

  #featured-products{
    width: 100%;
    margin: auto;
  }

  #featured-products img{
    flex-direction: column;
    margin: auto;
    width: 20%;
  }
}